<template>
  <div>
    <ion-card v-for="inf in info" :key="inf.cnum" class="card">
      <ion-header>
        <ion-subtitle>
          <!-- {{ info["cname"] }} -->
        </ion-subtitle>
        <ion-title>
          {{ inf.tphone}}
          
        </ion-title>
      </ion-header>
      <ion-card-content>
          <ion-list>
             <ion-item>
              <ion-lable>
                <strong> : </strong>
               {{ inf.tid }}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong>  : </strong>
               {{ inf.tname }}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong> : </strong>
              {{ inf.tdis}}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong>  : </strong>
                {{ inf.comp}}
              </ion-lable>
            </ion-item>
             <ion-item>
              <ion-lable>
                <strong>  : </strong>
                {{ inf.tdate}}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong>   : </strong>
                {{ inf.trep}}
              </ion-lable>
            
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong> : </strong>
                {{ inf.trepdate}}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong> : </strong>
                {{ inf.tman}}
              </ion-lable>
            </ion-item>
            <ion-item>
              <ion-lable>
                <strong>  : </strong>
                {{ inf.tstatus}}
              </ion-lable>
            </ion-item>
          </ion-list>
        </ion-card-content>
        
          <ion-input
            name="rep"
            v-bind:value="rep"
           @change="rep = $event.target.value"
            placeholder="  "
          ></ion-input>
            <ion-button @click="onrep" class="btnrep" expand="block"  color="new" >   <span class="infid">{{ inf.tid }}</span></ion-button>
         <ion-button @click="onFixed " class="btnsearch" value="500" color="new" expand="block" >   <span class="infid">{{ inf.tid }}</span></ion-button>
        
        
    </ion-card>
  </div>
</template>
<script>
export default {
  name: "maninfo",
   data() {
    return {
      tidfixed: "",
      rep:""
    };
   },
  props: ["info"],
  methods: {
    onFixed(e) {
      e.preventDefault();
      console.log(event);
      this.tidfixed=event.toElement.innerText.substring(20)
     // console.log("hello" + this.num);
        this.$emit("get-fixed",this.tidfixed);
         event.toElement.hidden = true;
        // this.$emit("get-num", this.name);
       // this.num = "";
    },
     onrep(e) {
      e.preventDefault();
      console.log(event);
      this.tidfixed=event.toElement.innerText.substring(20)
     // console.log("hello" + this.num);
        this.$emit("get-rep",this.tidfixed,this.rep);
        // event.toElement.hidden = true;
        // this.$emit("get-num", this.name);
       // this.num = "";
    }

  }
};
</script>
<style  scoped>
.card{
  float:right;
  text-align: right;
  direction: rtl;
  width:400px
}
.card ion-title{
  padding: 15px 60px;
  text-align: center;
  font-weight: bold;
  color:rgb(40, 43, 40);
  background-color: #42b883
}
.card ion-card-content{
  color:#1f82ec;
}
.card .infid{
  padding: 0px 5px;
}
.btnsearch{
  background-color: #35495e;
  margin: 40px 0px 0px;
  color:#fff;
}
.btnrep{
  float: left!important;
  background-color: #35495e;
  margin: 0px
}
@media only screen and (max-width: 890px) {
 .card{
    width:100%
} 
} 
</style>
