<template>
  <ion-grid class="grid">
    <form @submit="onSubmit">
      <ion-col>
        <ion-item>
          <ion-input
            name="num"
            v-bind:value="num"
           @change="num = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>: </ion-lable>
        </ion-item>
        <ion-item>
          <ion-input
            name="name"
            v-bind:value="name"
           @change="name = $event.target.value"
            placeholder="     "
          ></ion-input>
          <ion-lable>:  </ion-lable>
        </ion-item>
        <ion-item>
          <ion-input
            name="phone"
            v-bind:value="phone"
           @change="phone = $event.target.value"
            placeholder="   "
          ></ion-input>
          <ion-lable>:   /ip</ion-lable>
        </ion-item>
      </ion-col>
      
      <ion-col>
        <ion-button class="btnsearch" color="new" type="submit"  expand="block" >   </ion-button>
      </ion-col>
    </form>
  </ion-grid>
</template>
<script>
export default {
  name: "ZipSearch",
  data() {
    return {
      num: "",
      name:"",
      phone: ""
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
     // console.log("hello" + this.num);
        this.$emit("get-num", this.num, this.name,this.phone);
        // this.$emit("get-num", this.name);
       // this.num = "";
    }
  }
};
</script>
<style  scoped>
  .grid ion-input{
  text-align: right;
  padding:0px 20px!important;
}
.btnsearch{
  background-color: #42b883;
  margin: 0px
}


</style>
