<template>
  <div class="maintenance ion-page">
    <ion-header>
      <ion-toolbar>
        <ion-title>  -  </ion-title>
      </ion-toolbar>
      <navbar />
    </ion-header>

    <ion-content class="ion-padding">
      <CompSearch v-on:get-num="allRecords" />
      <ClearInfo v-bind:info="info" v-on:clear-info="clearInfo" />
      <ManInfo v-bind:info="info" v-on:get-fixed="fixed" v-on:get-rep="rep" />
      <!-- <button @click="allRecords">test</button> -->
    </ion-content>
  </div>
</template>
<script>
import CompSearch from "../components/CompSearch.vue";
import navbar from "../components/Navbar.vue";
import ManInfo from "../components/ManInfo.vue";
import ClearInfo from "../components/ClearInfo.vue";

export default {
  name: "maintenance",
  data() {
    return {
      info: "",
      num: "",
      name: "",
      tid: "",
      trep: "",
      tstatus: "",
     
      // trepdate: ""
    };
  },
  components: {
    navbar,
    CompSearch,
    ManInfo,
    ClearInfo
  },
  methods: {
    allRecords: function(phone, name, man) {
      //  this.clearInfo()
      let uri =
        "http://localhost/phpapi/sqlserver/complaint/search.php?p=" +
        phone +
        "&n=" +
        name +
        "&m=" +
        man;
      this.axios
        .post(uri)
        // .get("http://localhost/phpapi/sqlserver/customer/read.php")
        .then(response => {
          this.info = response.data;
          // console.log(this.info );
        })
        .catch(function(error) {
          console.log(error);
        });
       
    },
    clearInfo() {
      this.info = null;
    },

    fixed: function(tidfixed) {
      var currentdate = new Date()
        .toJSON()
        .slice(0, 10)
        .replace(/-/g, "/");
      this.trep = " ";
      this.tid= tidfixed;
      this.tstatus = "1";
     // console.log(tidfixed)
      let url = "http://localhost/phpapi/sqlserver/complaint/update.php";
      this.axios
        .post(url, {
          tid: this.tid,
          trep: this.trep,
          tstatus: this.tstatus,
          trepdate: currentdate
        })
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
       
        // this.clearInfo()
        // this.allRecords();
    },
    rep: function(tidfixed,rep) {
      var currentdate = new Date()
        .toJSON()
        .slice(0, 10)
        .replace(/-/g, "/");
      this.trep = rep;
      this.tid= tidfixed;
      
     // console.log(tidfixed)
      let url = "http://localhost/phpapi/sqlserver/complaint/update.php";
      this.axios
        .post(url, {
          tid: this.tid,
          trep: this.trep,
          trepdate: currentdate
        })
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
       
        // this.clearInfo()
        // this.allRecords();
    }
  }
};
</script>
<style scoped>
.ion-page ion-title {
  font-weight: bold;
  color: #42b883;
  padding: 0px;
}
.ion-pag ion-header {
  background-color: #35495e !important;
}
</style>
