<template>
  <ion-grid class="grid">
    <form @submit="onSubmit">
      <ion-col>
        <ion-item>
          <ion-input
            name="phone"
            v-bind:value="phone"
           @change="phone = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>: /ip</ion-lable>
        </ion-item>
         <ion-item>
          <ion-input
            name="name"
            v-bind:value="name"
           @change="name = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>:  </ion-lable>
        </ion-item>
         <ion-item>
          <ion-input
            name="dis"
            v-bind:value="dis"
           @change="dis = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>:  </ion-lable>
        </ion-item>
         <ion-item>
          <ion-input
            name="comp"
            v-bind:value="comp"
           @change="comp = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>:  </ion-lable>
        </ion-item>
         <ion-item>
          <ion-input
            name="rep"
            v-bind:value="rep"
           @change="rep = $event.target.value"
            placeholder="   "
          ></ion-input>
          <ion-lable>:   </ion-lable>
        </ion-item>
         <ion-item>
          <ion-input
            name="man"
            v-bind:value="man"
           @change="man = $event.target.value"
            placeholder="  "
          ></ion-input>
          <ion-lable>:  </ion-lable>
        </ion-item>
      </ion-col>
      <ion-col>
        <ion-button  class="btnsearch" color="new" type="submit"  expand="block" > </ion-button>
      </ion-col>
    </form>
  </ion-grid>
</template>
<script>
export default {
  name: "cusadd",
  data() {
    return {
      phone: "",
      name:"",
       dis: "",
      comp:"",
       rep: "",
      man:""
    
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
     
        this.$emit("get-add", this.phone, this.name, this.dis, this.comp, this.rep, this.man);
        // console.log("hello" + this.phone+ this.name+ this.dis+ this.comp+ this.rep+ this.man);
        // this.$emit("get-num", this.name);
       // this.num = "";
    }
   
  }
};
</script>
<style  scoped>
.grid ion-input{
text-align: right;
    padding:0px 20px!important;
}
.btnsearch{
  background-color: #42b883;
  margin: 0px
}


</style>
