<template>
  <div class="regfualt ion-page">
   <ion-header>
      <ion-toolbar>
        <ion-title>  -  </ion-title>
      </ion-toolbar>
       <navbar />
    </ion-header>
     <ion-content class="ion-padding">
      <CusAdd v-on:get-add="addRecords" />
      <CusSearch v-on:get-num="allRecords" />
      <ClearInfo v-bind:info="info" v-on:clear-info="clearInfo" />
      <CusInfo v-bind:info="info" />
      
      <!-- <button @click="allRecords">test</button> -->
    </ion-content>
  </div>
 
</template>
<script>
import navbar from "../components/Navbar.vue";
import CusSearch from "../components/CusSearch.vue";
import CusAdd from "../components/CusAdd.vue";
import CusInfo from "../components/CusInfo.vue";
import ClearInfo from "../components/ClearInfo.vue";
export default {
  name: "regfualt",
   data() {
    return {
        info: "",
        tname: "",
        tphone: "",
        tdis: "",
        tcomp: "",
        tdate: "",
        trep: "",
        trepdate: "",
        tstatus:"",
        tman: ""
    };
  },
  components: {
    navbar,
    CusSearch,
    CusAdd,
    CusInfo,
    ClearInfo
  },
   methods: {
    allRecords: function(phone, name) {
      //  this.clearInfo()
      let uri =
        "http://localhost/phpapi/sqlserver/complaint/search.php?p=" +
        phone +
        "&n=" +
        name 
       
      this.axios
        .post(uri
        )
        .then(response => {
          this.info = response.data;
          // console.log(this.info );
        })
        .catch(function(error) {
          console.log(error);
        });
       
    },
     addRecords: function(phone, name, dis, comp, rep, man) {
      var currentdate = new Date()
      // phone, name, dis, comp, rep, man
        .toJSON()
        .slice(0, 10)
        .replace(/-/g, "/");
      this.tname= name,
      this.tphone= phone,
      this.tdis= dis,
      this.tcomp= comp,
      this.tdate=currentdate,
      this.trep= rep,
      this.trepdate= currentdate,
      this.tstatus= "5",
      this.tman= man
     
//      let instance =this.axios.create();

// // delete headers if existing
// delete instance.defaults.headers.common['Accept'];
// delete instance.defaults.headers.common['X-Requested-With'];
// delete instance.defaults.headers.common['X-CSRF-TOKEN'];
      // console.log("hello" + phone+ name+ dis+ comp+ rep+ man);
      let url = "http://localhost/phpapi/sqlserver/complaint/create.php";
      this.axios
        .post(url, {
          tname: this.tname,
          tphone: this.tphone,
          tdis: this.tdis,
          tcomp: this.tcomp,
          tdate: this.tdate,
          trep: this.trep,
          trepdate: this.trepdate,
          tstatus: this.tstatus,
          tman: this.tman
          }, {
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }

        })
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
       
    },
    clearInfo() {
      this.info = null;
    },
   }
};
</script>
<style scoped>
.ion-page ion-title {
  font-weight: bold;
  color: #42b883;
  padding: 0px;
}
.ion-pag ion-header {
  background-color: #35495e !important;
}
</style>